// == Import
import {  Box } from '@mui/material'
import { useSelector } from 'react-redux'
import NFT from './NFT'
// == Composant
function NFTS() {
  const nfts = useSelector(state => state.nfts)
 const nftsUser = nfts.filter(nft => {
   if (nft.blockchain === 'ETHEREUM'){
      return nft
   }
   
  })
console.log(nftsUser)
  return (
      <Box sx={{}}>
      {nftsUser.map(nft => (
       <NFT 
        key={nft.id}
        image= {nft.meta.content[0].url}
       />

      ))}
      </Box>
      
  

  );
}

// == Export
export default NFTS;
